@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.8.3/font/bootstrap-icons.css");
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}
body{
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: #f0efea;
}
.main{
  background-color: #fff;
  padding: 30px 40px;
  width: 400px;
  max-height: 435px;
  text-align: center;
  position: relative;
  transition: .3s;
}
header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.date{
  display: flex;
  gap: 5px;
  align-items: center;
}
.right{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.left{
  font-size: 3em;
}
.weekDay{
  text-transform: uppercase;
  font-weight: bold;
}
.month{
  text-transform: uppercase;
}
.item{
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.addBtn{
  width: 54px;
  margin: 0 auto -60px;
  height: 54px;
  background-color: #51e3a4;
  color: #42c08b;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  font-size: 2em;
  cursor: pointer;
  transition: .3s;
  user-select: none;
}
.addBtn:hover{
  box-shadow: 0 0 8px #fff;
  transform: scale(1.2, 1.2);
}
.items{
  margin: 15px 0;
  overflow-y: auto;
  max-height: 285px;
  padding-right: 10px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.items::-webkit-scrollbar {
  width: 3px;
}

.items::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  border-radius: 10px;
}
.item{
  transition: .3s;
  cursor: pointer;
  border-bottom: 1px solid #fff;
  padding-bottom: 5px;
}
.item:hover{
  border-bottom: 1px solid #222;
}
.item:hover .other{
  visibility: visible;
}

#checkbox{
  display: none;
}
#checkbox:checked ~ .circle{
  border: 2px solid #51e3a4;
}
.circle{
  transition: .3s;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid #666;
  display: flex;
  justify-content: center;
  align-items: center;
}
#checkbox:checked ~ .circle .point{
  width: 10px;
  height: 10px;
  background-color: #42c08b;
  border-radius: 50%;
}
#checkbox:checked ~ span{
  color: #bcbcbc;
}
.other{
  visibility: hidden;
  transition: .3s;
  display: flex;
  gap: 20px;
}
.bi-trash:hover{
  color: #f00;
}
.bi-pencil-square:hover{
  color: #aaa;
}
.addTask{
  background-color: #fff;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 5px;
  padding-right: 0 ;
  transition: .3s;
}
#addTask{
  border: none;
  outline: none;
  box-shadow: 0 0 4px #aaa;
}
.ok{
  padding: 5px 10px;
  border-radius: 5px;
  border: 1px solid #000;
  color: #000;
  cursor: pointer;
  user-select: none;
  transition: .3s;
}
.ok:hover{
  background-color: #42c08b;
  color: #fff;
  box-shadow: 0px 0px 5px #aaa;
  border: 1px solid #42c08b;
}

.esc{
  padding: 5px 10px;
  border-radius: 5px;
  border: 1px solid #000;
  color: #000;
  cursor: pointer;
  user-select: none;
  transition: .3s;
}
.esc:hover{
  background-color: #6b706e;
  color: #fff;
  box-shadow: 0px 0px 5px #aaa;
  border: 1px solid #6b706e;
}
.noTasks{
  margin: 20px 0;
  color: #aaa;
}